.hidden {
  display: none;
}

.loader {
  width: 28px;
  height: 28px;
  box-sizing: border-box;
  border: 3px solid #6991ef;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

